html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 90px; /* Margin bottom by footer height + 30*/
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px; /* Set the fixed height of the footer here */
  line-height: 50px; /* Vertically center the text there */

  > div > span > a {
    color: white;

    &:hover {
        color: white;
    }
  }
}